<template lang='pug'>
    .div
        .container
            .card
                .card-content
                    h1(style="font-family:Quattrocento;font-size: 3em; font-weight: bold;line-height:1.2em") Hi !
                    hr
                    .content(style="display:flex")
                        .column.is-6
                          img.avatar(src="../assets/avatar.jpeg")
                        .column.is-6
                            p Hi and welcome to my IP Assignment website. I am Abdul also known as YourFavouriteOreo in more geek circles.
                            br
                            p I tend to delve my time into different avenues of technology and I can't ever stick to something so I have dipped my toes into everything from Web development, App Development and even Game development (using Unreal Engine).
            .card
                .card-content
                    .content(style="display:flex")
                        .column
                            h1(style="font-family: Quattrocento;") How did I get the Internship?
                            hr
                            p I applied , intervied and done diddly did. (Jokes)
                            br
                            p Vertex is owned by a family relative and as such I had a network into the company. After conversations with him, he asked me for my work and was pleased with previous results I have had with other employers.
                            br
                            p After a few more conversations, we struck a deal on my internship pay, hours and I was locked in for my internship.
            .card
                .card-content
                    .content(style="display:flex")
                        .column
                            h1(style="font-family: Quattrocento;text-shadow: 1px 1px 3px #222222;") My&nbsp;
                                span(style="font-family: Almendra Display;font-weight:bold") Internship Adventure
                            hr
                            p My Internship is with an investment house in Dar es Salaam called&nbsp;
                                b Vertex Capital Limited.
                            br
                            p Vertex was looking into expanding themselves in various tech industries as well as cementing themselves digitally with websites to advertise themselves . I was brought in as a software engineer intern to start this process and the creation of their digital representation.
                            br
                            p I accepted the offer with Vertex Capital Limited due to not only seeing the potential in Vertex Capital Limited as a business but also for the impact it has in my community due to the fact that multiple of the businesses supported by Vertex improve or supply exclusively to the local communities and neighbouring countries.
                            br
                            p I also believed that I needed to work with a relatively young company to understand the struggles such companies go through as it is important to have a wide and diverse variety of perspective to ensure that all future software that I create accurately solves problems for companies of all shapes and sizes.
</template>

<script>
export default {
  name: 'HomePage',
  data () {
    return {
    }
  },
  components: {
  },
  methods: {
  }
}

</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="sass" >
@import "@/assets/vars.sass"

.avatar
    height: 250px
    width: 250px
    border-radius: 250px

.card
    width: 50%;
    margin: 1rem auto
    border-radius: 20px
    box-shadow: inset 1px 2px 20px 0px #d5d5d5c4;

.card-content
    padding: 0.3rem

.column
    padding: 0.5rem 0.5rem

p
    font-family: "Lato", sans-serif;

hr
    margin: 0.5em auto
    margin-bottom: 1em
    width: 60%
</style>



